.about-us {
    box-sizing: border-box;
    padding: 0 103px;
    margin-bottom: 132px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    
    &__promo {
      width: auto;
      min-width: 512px;
      margin-right: 75px;
      
    }
    &__title {
      margin: 0;
      padding: 0;
      margin-bottom: 40px;
      font-size: 28px;
      font-weight: 600;
    }
    &__description {
      margin: 0;
      padding: 0;
    }
  
}

@media (max-width: 1023px) {
  .about-us {
    padding: 0 60px;
    img {
      width: 324px;
      height: 264px;
      object-fit: cover;
    }
    &__promo {
      min-width: 325px;
    }
  }
}
@media(min-width: 1367px) {
  .about-us {
    padding: 0 calc(50vw - 580px);
  }
}
@media (max-width: 767px) {
  .about-us {
    margin: 0;
    padding: 0px 15px 65px 15px;
    flex-direction: column;
    align-items: initial;
    img {
      width: auto;
      object-fit: contain;
    }
    &__promo {
      margin: 0;
      min-width: 290px;
      margin-bottom: 35px;
    }
    &__title {
      text-align: center;
      font-size: 24px;
      margin-bottom: 25px;
    }
  }
}